import React from 'react';
import { Navbar, Container, Nav } from 'react-bootstrap';
import '../style/NavBar.scss';

const ReactNavBar = () => {
    
    return (
        <Navbar className='navBar' collapseOnSelect expand="lg" bg='dark' variant="dark" sticky='top'>
            <Container>
                <Navbar.Brand className='brand' href="/EtanMuller">Etan Muller</Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="me-auto"></Nav>
                    <Nav>
                    <Nav.Link  href={'/EtanMuller'}>Accueil</Nav.Link>
                        <Nav.Link  href={'/EtanMuller/disponibilites'}>Rserver</Nav.Link>
                        <Nav.Link href="#infos">Infos</Nav.Link>
                        <Nav.Link href="#equipement">Dtails</Nav.Link>
                        <Nav.Link href="#about">About us</Nav.Link>
                        <Nav.Link href="#contact">Contact</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
};

export default ReactNavBar;