
import { Paper, Accordion, AccordionSummary, Typography, Breadcrumbs, Link } from '@mui/material'
import React, { useEffect, useState } from 'react';
import { getAllDisponobilities } from '../dataAcces/ApiDatas';
import { Container } from 'react-bootstrap';
import { containsDispo, euro, racineURL } from '../util/util';
import '../style/Disponibilit.scss';
import Footer from './Footer';
import ReactNavBar from './ReactNavBar';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import { styled } from '@mui/material/styles';


const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
    padding: theme.spacing(2),
    borderTop: '1px solid rgba(0, 0, 0, .125)',
}));



const Disponibilit = () => {
    const [dispos, setDispos] = useState([]);
    const [selections, setSelections] = useState([]);

    useEffect(() => {

        getAllDisponobilities().then(datas => setDispos(datas));

    }, []);


    const handleChoiceDispo = (dispo) => {
        const it = {position: -1};

        if (containsDispo(selections, dispo, it)){
            selections.splice(it.position, 1);
        }else{
            setSelections([dispo, ...selections]);
        }
    }

    console.log("selections : ", selections);

    return (
        <div className='global'>
            <ReactNavBar />

            <div className='header'>
                <img src={`${racineURL}/carousel/carousel1.jpg`} alt="header" />
            </div>

            <Container className="mt-4">

                <Breadcrumbs separator='>'>
                    <Link className='breadcrumb-not-active' href='/EtanMuller'>Accueil</Link>
                    <Typography>Disponibilits</Typography>
                </Breadcrumbs>
            </Container>

            <Container className="disponibilit">

                {dispos.length > 0 && (
                    dispos.map((d, key1) => {
                        return (
                            d.dates.length !== 0 && (
                                <div className='ligne'>
                                    <Accordion className='accordion'>
                                        <AccordionSummary
                                            expandIcon={<i className='fas fa-calendar d-flex' ></i>}
                                            aria-controls="panel1b"
                                            id={key1}
                                        >
                                            <Typography sx={{ width: '33%', flexShrink: 0 }}>
                                                {d.month}
                                            </Typography>
                                            <Typography sx={{ color: 'text.secondary' }}>{d.price} {euro}/semaine</Typography>
                                        </AccordionSummary>
                                        <AccordionDetails>
                                            <ul>
                                                {
                                                    d.dates.map((date, key2) => {
                                                        return (

                                                            <li id={key2} key={key2}>
                                                                <span><input onChange={() => handleChoiceDispo({month: d.month, price: d.price, date})} type="checkbox" name="check" /> Du</span>
                                                                <Paper className={'NotselectedBg'} elevation={2}> {date.checkIn} </Paper>
                                                                <span>Au</span>
                                                                <Paper className={'NotselectedBg'} elevation={2}> {date.checkOut} </Paper>
                                                            </li>

                                                        );
                                                    })
                                                }
                                            </ul>
                                        </AccordionDetails>
                                    </Accordion>
                                </div>
                            )
                        );
                    })
                )}
                <div className='sectionWave'>
                    <div className="wave"></div>
                    <div className="wave"></div>
                    <div className="wave"></div>
                </div>
                <button className='button'>Rserver</button>
            </Container >

            <Footer />
        </div>
    );
};

export default Disponibilit;