import React, { useCallback, useEffect, useState } from 'react';
import { Card, Carousel, Container, FloatingLabel, Form, Modal } from 'react-bootstrap';
import { addComment, commentaires, getAllComments } from '../dataAcces/ApiDatas';
import { Avatar, Rating } from '@mui/material';
import '../style/Commentaire.scss';

const Commentaire = () => {

    const [commentPerPage, setCommentPerPage] = useState(window.innerWidth < 814 ? 1 : 3);
    const [comments, setComments] = useState([]);
    let [currentPage] = useState(1);
    const [commentsToShow] = useState([]);
    const [commentText, setCommentText] = useState('');
    const [autorComment, setAutorComment] = useState('');
    const [isOpen, setModal] = useState(false);
    const [nbStar, setNbStar] = useState(0);
    const [msgError, setMsgError] = useState('');
    //const [frameWidth, setFrameWidth] = useState(0);


    const detectWidth = useCallback(() => {
        if (window.innerWidth < 814) {
            setCommentPerPage(1);
        } else
            setCommentPerPage(3);
    }, []);

    const setCommentToShow = useCallback((c) => {
        let end = currentPage * commentPerPage;
        let start = end - commentPerPage;

        for (let i = start; i < Math.ceil(c.length / commentPerPage); i++) {
            commentsToShow[i] = c.slice(start, end);

            currentPage++;
            end = currentPage * commentPerPage;
            start = end - commentPerPage;
        }

    }, [commentPerPage, currentPage, commentsToShow]);

    useEffect(() => {

        getAllComments().then(datas => {
            setComments(datas);
            
        });

        detectWidth();
        setCommentToShow(comments);
        
    }, [detectWidth, setCommentToShow, comments]);

    const handleClick = (e) => {
        e.preventDefault();
        
        if (nbStar > 0) {
            const newComment = {
                commentaire: commentText,
                auteur: autorComment,
                nombreEtoiles: nbStar
            };

            addComment(newComment).then(() => console.log("commentaires ajout !"));
            msgError !== '' && setMsgError('');
            setCommentText('');
            setModal(false)
    
        } else {
            setMsgError("Veuiller laisser une note s'il vous plait !")
        }
    }

    return (
        <Container fluid className='commentaires'>
            <h1>Tmoignages</h1>
            <Container fluid className='container'>
                <Carousel className='carousel'>
                    {
                        commentsToShow.map((comments, k) => {
                            return (
                                <Carousel.Item>
                                    <div key={k} className="comments">
                                        {comments.map((c, key) => {
                                            return (
                                                <div key={key}>
                                                    <Card className='card'>
                                                        <div className="header">
                                                            <Card.Title className='card-title'>
                                                                <div className="avatar"><Avatar className='av' /></div>
                                                                <div> <span className='autor-name'> {c.auteur} </span> </div>
                                                            </Card.Title>
                                                            <Card.Subtitle className='subTitle'>
                                                                <Rating
                                                                    className='star'
                                                                    name='simple-controlled'
                                                                    value={c.nombreEtoiles}
                                                                    readOnly
                                                                    max={5}
                                                                />
                                                            </Card.Subtitle>
                                                        </div>
                                                        <Card.Body className='text-comment'>
                                                            {c.commentaire}
                                                        </Card.Body>
                                                    </Card>
                                                </div>
                                            );
                                        })}
                                    </div>
                                </Carousel.Item>
                            );
                        })
                    }

                </Carousel>

            </Container>
            <div className='write-comments'>

                <Form.Group className='formGroup'>
                    <FloatingLabel id='floatingTextarea' controlId="floatingTextarea" label='Comment'>
                        <Form.Control value={commentText} onChange={(e) => setCommentText(e.target.value)} as='textarea' />
                    </FloatingLabel>
                    <button onClick={() => setModal(true)} disabled={commentText === ''} className='btn-comments m-4'>Publier un avis</button>
                </Form.Group>

            </div>

            <Modal className='modal' show={isOpen} onHide={() => {
                setMsgError(' ');
                setModal(false);
            }}>
                <Modal.Title>Votre Nom</Modal.Title>
                <div className="modal-body">
                    <Form onSubmit={(e) => handleClick(e)}>
                        <div>
                            <FloatingLabel id='floatingInput' label='Your name'>
                                <Form.Control value={autorComment} required onChange={(e) => setAutorComment(e.target.value)} type='text' />
                            </FloatingLabel>
                        </div>

                        <div>
                            <div>Note</div>
                            <Rating
                                onChange={(event, value) => {
                                    msgError !== '' && setMsgError('');
                                    setNbStar(value);
                                }}
                                className='star'
                                id='star'
                                name='simple-controlled'
                                value={nbStar}
                                max={5}
                            />
                            <p className='error-msg'> {msgError} </p>
                        </div>
                        <button className='btn-comments'>Publier</button>
                    </Form>
                </div>

            </Modal>
        </Container>
    );
};

export default Commentaire;