

export const euro = "";
export const racineURL = `${process.env.PUBLIC_URL}/images`;
export const root = "localhost:3000/EtanMuller";

/**
 * selections est un tableau d'objet : {month, price, dateSelectione}
 */

export const applyReduction = (selections) => {
    let output = { total: 0, priceToShow: 0 }

    selections.array.forEach(s => {
        output.total += s.price;
        if (s.month === 'Juillet' || s.month === 'Aout') {
            output.priceToShow += s.price;
        }
    });

    let nbReduction = output.priceToShow / 1000;
    output.priceToShow -= (nbReduction * 50);

    return output;
}

export const containsDispo = (dispos, selection, {position}) => {
    let i = 0;

    while (i < dispos.length && (dispos[i].month !== selection.month || (dispos[i].date.checkIn !== selection.date.checkIn || dispos[i].date.checkOut !== selection.date.checkOut)))
        i++;

    position = i;

    return i < dispos.length;
}