import React from 'react';
import { Card, Col, Container, Row } from 'react-bootstrap';
import '../style/Equipement.scss';
import { euro, racineURL } from '../util/util';

const Equipement = () => {
    return (
        <div id='equipement' data-aos="fade-up" className='equipement'>
            <h1 className='text-uppercase'>Equipements</h1>
            <div className='trait'><hr /></div>
            <div className='blockCenter'>
                <div className='blockIcon'>
                    <div className='icon'>
                        <img src={`${racineURL}/icon/sofa.png`} alt="sofa" />
                        <h5>1 Salon</h5>
                    </div>
                    <div className='icon'>
                        <img src={`${racineURL}/icon/bed.png`} alt="sofa" />
                        <h5>1 Chambre</h5>
                    </div>
                    <div className='icon'>
                        <img src={`${racineURL}/icon/kitchen.png`} alt="sofa" />
                        <h5>1 Cuisine</h5>
                    </div>
                    <div className='icon'>
                        <img src={`${racineURL}/icon/bathtub.png`} alt="sofa" />
                        <h5>1 Salle de bain</h5>
                    </div>
                </div>
            </div>

            <Container>
                <Row className='sectionDetail'>
                    <Col data-aos='slide-up' sm={1} md={3} as='div'>
                        <Card className='card'>
                            <Card.Body>
                                <ul>
                                    <li>Appartement T2 pour 4  6 personnes</li>
                                    <li>Situ au 1er tage avec parking</li>
                                    <li>Gardien de scurit jour et nuit</li>
                                    <li>Hotspot wifi extrieur gratuit</li>

                                </ul>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col data-aos='slide-down' sm={1} md={3} as='div'>
                        <Card className='card'>
                            <ul>
                                <li>Salon + canap-lit pour 2 personnes</li>
                                <li>Matlas gonflable pour 2 personnes</li>
                                <li>Cambre avec lit superppos</li>
                                <li>4 Places espace aquatisue gratuit</li>
                                <li>1 Balcon</li>
                            </ul>
                        </Card>
                    </Col>
                    <Col data-aos='slide-up' sm={1} md={3} as='div'>
                        <Card className='card'>
                            <ul>
                                <li>TV cran plat</li>
                                <li>Refrgirateur</li>
                                <li>Micro-ondes</li>
                                <li>Machine  laver</li>
                                <li>Un schoir  linge pliable</li>
                                <li>Machine  caf Senso</li>
                                <li>Barbecue lectrique</li>
                            </ul>
                        </Card>
                    </Col>
                    <Col data-aos='slide-down' sm={1} md={3} as='div'>
                        <Card className='card'>
                            <ul>
                                <li>Les animaux ne sont pas accepts</li>
                                <li>Appartement non fumeur (Fumer  l'extrieur)</li>
                                <li>
                                    Location Box 4G wifi nomade
                                    <ul>
                                        <li>Jusqu'a 10 appareils</li>
                                        <li>7{euro}/jr ou 39{euro}/semaine</li>
                                        <li>Caution de 150{euro}  dposer</li>
                                    </ul>
                                </li>
                            </ul>
                        </Card>
                    </Col>
                </Row>

            </Container>
        </div>
    );
};

export default Equipement;