import React from 'react';
import { Carousel } from 'react-bootstrap';
import { euro } from '../util/util';
import '../style/Carousel.scss'
import { useNavigate } from 'react-router-dom';

const ReactCarousel = () => {
    const car1 = `${process.env.PUBLIC_URL}/images/carousel/carousel1.jpg`;
    const car2 = `${process.env.PUBLIC_URL}/images/carousel/carousel2.jpg`;
    const car3 = `${process.env.PUBLIC_URL}/images/carousel/carousel3.jpg`;
    const navigate = useNavigate();

    const handleClick = () => {
        navigate('/disponibilites');
    }

    return (
        <Carousel className='carousel' fade controls={false} indicators={false} >
            <Carousel.Item>
                <img
                    className="d-block w-100 carouselImg"
                    src={car1}
                    alt="Second slide"
                />
                <Carousel.Caption className='caption'>
                    <h1 className='titleCaption'>Etan Muller <br /> sur la cote de france</h1>
                    <h3 className='priceCaption'>A partir de 250 {euro} / semaine</h3>
                    <p className='textCaption'>Lorem ipsum dolor sit amet consectetur adipisicing elit. Animi doloribus
                        itaque necessitatibus eos. Obcaecati ut qui repellat ipsum placeat explicabo.
                    </p>
                    <button onClick={handleClick} className='buttonCaption'>Rserver</button>
                </Carousel.Caption>
            </Carousel.Item>

            <Carousel.Item>
                <img
                    className="d-block w-100 carouselImg"
                    src={car2}
                    alt="Second slide"
                />
                <Carousel.Caption className='caption'>
                    <h1 className='titleCaption'>Passez des vacances de rves</h1>
                    <h3 className='priceCaption'>A partir de 250 {euro} / semaine</h3>
                    <p className='textCaption'>Lorem ipsum dolor sit amet consectetur adipisicing elit. Animi doloribus
                        itaque necessitatibus eos. Obcaecati ut qui repellat ipsum placeat explicabo.
                    </p>
                    <button className='buttonCaption'>Rserver</button>
                </Carousel.Caption>
            </Carousel.Item>

            <Carousel.Item>
                <img
                    className="d-block w-100 carouselImg"
                    src={car3}
                    alt="Second slide"
                />
                <Carousel.Caption className='caption'>
                    <h1 className='titleCaption'>Un endroit plus que paradisiaque</h1>
                    <h3 className='priceCaption'>A partir de 250 {euro} / semaine</h3>
                    <p className='textCaption'>Lorem ipsum dolor sit amet consectetur adipisicing elit. Animi doloribus
                        itaque necessitatibus eos. Obcaecati ut qui repellat ipsum placeat explicabo.
                    </p>
                    <button className='buttonCaption'>Rserver</button>
                </Carousel.Caption>
            </Carousel.Item>
        </Carousel>

    );
};

export default ReactCarousel;